<template>
	<div class="body">
		<div class="body_header flex_start">
            <i class="icon iconfont icon-tianjiaguanligongsi"></i>
			<span>添加管理公司</span>
		</div>
		<div class="block_title flex_between">
			<div class="title_left flex_start">
				<div class="radio"> </div>
				<div class="block_name">
					公司基本信息
				</div>
			</div>
		</div>
		<!--添加管理公司内容开始-->
		<div class="main">
			<div class="form flex_between">
				<div class="form_list flex_between">
					<div class="flex_start">
						<span>管理公司名称</span>
                			<!--<i class="icon iconfont icon-bitian"></i>-->
					</div>
					<div class="">
						<el-select v-model="company_name" :remote-method="remoteMethod" @change="changeName" :loading="loading" filterable remote :clearable='true' reserve-keyword placeholder="请输入公司名称">
							<el-option v-for="item in tianYanBackData" :key="item.name" :label="item.name" :value="item.name">
							</el-option>
						</el-select>
					</div>
				</div>
				<!--<div class="form_list flex_between">
					<div class="">
						<span>统一社会信用代码</span>
                			<i class="icon iconfont icon-bitian"></i>
					</div>
					<div class="">
						<el-input placeholder="请输入内容" v-model="credit_code" clearable>
						</el-input>
					</div>
				</div>-->
				<div class="form_list flex_between">
					<div class="">
						<span>运营风控负责人</span>
					</div>
					<div class="">
						<el-select v-model="windcontrol_userid" filterable clearable placeholder="请选择">
						    <el-option v-for="item in employee" :key="item.id" :label="item.name" :value="item.id">
						    </el-option>
						</el-select>
					</div>
				</div>
				<div class="form_list flex_between">
					<div class="">
						<span>管理公司简称</span>
					</div>
					<div class="">
						<el-input placeholder="请输入内容" v-model="short_name" clearable>
						</el-input>
					</div>
				</div>
				<div class="form_list flex_between">
					<div class="">
						<span>数据创建人</span>
					</div>
					<div class="">
						<el-select v-model="create_user_id" filterable clearable placeholder="请选择">
						    <el-option v-for="item in employee" :key="item.id" :label="item.name" :value="item.id">
						    </el-option>
						</el-select>
					</div>
				</div>
				<div class="form_list flex_between">
					<div class="">
						<span>所属地区</span>
					</div>
					<div class="">
						<el-input placeholder="请输入内容" v-model="area" clearable>
						</el-input>
					</div>
				</div>
				<div class="form_list flex_between">
					<div class="">
						<span>备案状态</span>
					</div>
					<div class="">
						<el-select v-model="record_state" clearable placeholder="请选择">
						    <el-option v-for="item in beianStatus" :key="item.id" :label="item.name" :value="item.id">
						    </el-option>
						</el-select>
					</div>
				</div>
				<div class="form_list flex_between">
					<div class="">
						<span>私募基金管理人编号</span>
					</div>
					<div class="">
						<el-input placeholder="请输入内容" v-model="manager_code" clearable>
						</el-input>
					</div>
				</div>
				<!-- <div class="form_list flex_between">
					<div class="">
						<span>法定代表人</span>
					</div>
					<div class="">
						<el-input placeholder="请输入内容" v-model="legal_representative" clearable>
						</el-input>
					</div>
				</div> -->
				<!-- <div class="form_list flex_between">
					<div class="">
						<span>成立时间</span>
					</div>
					<div class="">
						<el-date-picker v-model="founding_time" type="date" placeholder="选择日期" format="yyyy年MM月dd日" value-format="yyyy-MM-dd">
    						</el-date-picker>
					</div>
				</div> -->
			</div>
			<div class="form_btn flex_end">
				<button @click="submitAddCompany">创建</button>
			</div>
		</div>
	</div>
</template>

<script>
	import { getBeiAnStatusApi,getUserlistApi,submitAddCompanyApi,getTianyanIdApi } from '../../config/api'
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	
	export default {
		name: 'addManageCompany',
		data() {
			return {
				beianStatus: [],
				employee: [],
				tianYanBackData: [],
				loading: false,
				tianYanSelectedItem: {},
				// 表单字段
				windcontrol_userid: '',//新增字段
				company_name: '',
				tianyan_id: '',
				credit_code: '',
				short_name: '',
				founding_time: '',
				record_state: '',
				area: '',
				create_user_id: '',
				manager_code: '',
				legal_representative: '',
			}
		},
		created() {
			this.getBeiAnStatus()
			this.getUserList()
		},
		mounted() {},
		components: {},
		methods: {
			getBeiAnStatus(){
				getBeiAnStatusApi({
					
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.beianStatus = res.data.payload.record_state;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			getUserList(){
				getUserlistApi({
					number: 10000,
					page: 1
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.employee = res.data.payload.data;
						this.create_user_id = parseInt( window.localStorage.getItem('htcx_uid')) ;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			submitAddCompany(){
				
				if (this.company_name) {
					submitAddCompanyApi({
						company_name: this.company_name,
						tianyan_id: this.tianyan_id,
						short_name: this.short_name,
						create_user_id: this.create_user_id,
						windcontrol_userid: this.windcontrol_userid,
						area: this.area,
						record_state: this.record_state,
						founding_time: this.founding_time,
						legal_representative: this.legal_representative,
						manager_code: this.manager_code,
						
						credit_code: this.credit_code,//因后台需求传固定值
					}).then(res => {
						if(res.data.msgNo == 1) {
							dialogConfirm("success", res.data.message);
							this.$router.push({name: "manageCompanySituation"})
						} else {
							dialogConfirm("info", res.data.message);
						}
					})
				}else {
					let info = '管理公司名称不能为空';
					dialogConfirm("info", info);
				}
				
			},
			// 获取天眼查返回的数据
			remoteMethod(query) {
				if(query !== '') {
					this.loading = true;
					getTianyanIdApi({
						name: query
					}).then(res => {
						this.loading = false;
						if(res.data.msgNo == 1) {
							if(res.data.payload.length == 0) {
								this.tianYanBackData = [{
									name: query
								}]
							} else {
								let newObj = {
									name: query
								}
								res.data.payload.unshift(newObj);
								this.tianYanBackData = res.data.payload;
							}

						}
					})
				} else {
					this.tianYanBackData = [];
				}
			},
			changeName(e){
				this.getTianYanId()
			},
			getTianYanId(){
				var data = this.tianYanBackData;
				for (var i = 0,max = data.length;i<max;i++) {
					if (data[i].name == this.company_name) {
						this.tianyan_id = data[i].id;
						if(!this.tianyan_id){
							this.tianyan_id = '';
						}
						return
					}
				}
			},
			isTrueCreditCode(str){
				var reg = /^[^_IOZSVa-z\W]{2}\d{6}[^_IOZSVa-z\W]{10}$/g;
				if (reg.test(str)) {
					return true
				} else{
					return false
				}
			}
		},
	}
</script>

<style scoped="scoped">
	.body{
		padding: 0 30px 30px 30px;
	}
	.page_name{
		margin-bottom: 40px;
		
	}
	.page_name i{
		font-size: 16px;
		color: #6292F4;
	}
	.block_title {
		margin-bottom: 25px;
		padding-bottom: 10px;
		border-bottom: 1px solid #CECBC9;
	}
	
	.title_left>div {
		color: #6292F4;
		font-size: 16px;
	}
	
	.title_left .radio {
		width: 7px;
		height: 7px;
		margin-right: 12px;
		border-radius: 50%;
		background: #6292F4;
	}
	.form{
		margin-bottom: 40px;
		flex-wrap: wrap;
		box-sizing: border-box;
		padding: 0 30px 0 10px;
	}
	.form_list{
		width: 35%;
		margin-bottom: 20px;
		display: inline-flex;
	}
	.form_list span{
		color: #6F7071;
		font-size: 14px;
	}
	.form_list i{
		color: #FF5858;
		margin-left: 5px;
	}
	.form_list input{
		width: 240px;
		height: 40px;
		padding: 0 15px;
		font-size: 14px;
		color: #33302D;
		border-radius: 20px;
		background: #F3F6FB;
	}
	.form_list>div:nth-child(2){
		width: 250px;
	}
	.form_btn{
		padding-right: 30px;
	}
	.form_btn button{
		width: 94px;
		height: 36px;
		color: white;
		font-size: 14px;
		border-radius: 20px;
		background: #6292F4;
	}
</style>


